.function-wrapper{
    padding: 110px 0;
    position: relative;
    background-color: var(--colors-primary);
    background-image: url(data:image/png;base64,);
    color: #fff;
    padding: 110px 0;
    z-index: 1;
}
.function-wrapper::before{
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    background: linear-gradient(-45deg,rgba(42,72,125,0.3) 25%,transparent 25%,transparent 50%,rgba(42,72,125,0.3) 50%,rgba(42,72,125,0.3) 75%,transparent 75%,transparent);
    width: 100%;
    background-size: 350px 350px;
    height: 100%;
    opacity: 0.3;
    z-index: -1;
}
.function-head{
    text-align: center;
}
.function-head h5{
    font-size: 14px;
    letter-spacing: 1px;
    margin-bottom: 10px;
    opacity: 0.6;
}
.function-head h3{
    font-size: 32px;
    margin-bottom: 30px;
}
.function-items{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
}
.function-item-index{
    width: 60px;
    height: 60px;
    background-color: #fff;
    color: var(--colors-primary);
    margin-bottom: 25px;
    font-size: 28px;
    font-weight: 700;
    text-align: center;
    line-height: 60px;
    border-radius: 20px;
}
.function-item-title{
    font-size: 20px;
    margin-bottom: 15px;
}
.function-item-desc{
    font-size: 15px;
    line-height: 28px;
    opacity: 0.6;
}
@media screen and (min-width: 1220px) {
    
.function-item{
    
    position: relative;
}
.function-item::before{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-position: center center;
    width: 215px;
    height: 60px;
    opacity: 0.3;
    left: 70px;
}
.function-item:nth-of-type(2n-1)::before {
    background-image: url(data:image/png;base64,);
}
.function-item:nth-of-type(2n-2)::before {
    background-image: url(data:image/png;base64,);
    top: 17px;
}
.function-item:last-child::before{
    background-image: none;
}
}
@media screen and (max-width: 1024px) {
    .function-items{
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    }
}
@media screen and (max-width: 693px) {
    .function-item{
        text-align: center;
    }
    .function-item-index{
        margin:0 auto 25px;
    }
}